import {Component, OnInit} from '@angular/core';
import {Storage} from "@ionic/storage";
import {ApiService} from "../../services/api.service";
import {AuthService} from "../../services/auth.service";
import {LoaderService} from "../../services/loader.service";
import {ToastService} from "../../services/toast.service";
import {Location} from "@angular/common";
import * as moment from 'moment';
import {Platform, MenuController} from "@ionic/angular";
import {Device} from "@ionic-native/device/ngx";
import {AnalyticsService} from "../../services/analytics.service";
import {TranslateService} from "@ngx-translate/core";
import { EventProviderService } from 'src/services/event-provider.service';

@Component({
    selector: 'app-my-groups',
    templateUrl: './my-groups.page.html',
    styleUrls: ['./my-groups.page.scss'],
})
export class MyGroupsPage implements OnInit {

    loginData: any;
    groupsdata = false;
    dataLoaded = false;
    data: any[] = [];
    start_time_analytics: any;
    page_name: any = "My Groups";
    custom_name: any = "My Groups";
    user: any;
    theme: any;
    alertMessage: any;
    constructor(
        private apiService: ApiService,
        private storage: Storage,
        private loader: LoaderService,
        private Auth: AuthService,
        private toast: ToastService,
        private location: Location,
        private analytics: AnalyticsService,
        private platform: Platform,
        private device: Device,
        private translate: TranslateService,
        private menu: MenuController,
        public event_provider:EventProviderService,
    ) {
        // get translated alert message
        let interval = setInterval(() => {
            this.translate.get('ALERTS').subscribe( (res: any) => {
                this.alertMessage = res;
                clearInterval(interval);
            });
        }, 2000)
    }

    ngOnInit() {
        // // getting user data
        // this.getLoginData();

        // getting theme from local storage
        this.storage.get('theme').then(async res => {
            this.theme = res;
        });
    }

    async getLoginData() {
        this.loginData = await this.Auth.getUser();
        if (this.loginData) {
            // getting groups data
            this.getGroupData();
        }
    }

    // getting groups data

    getGroupData() {
            const body = {
                auth: 'Bearer ' + this.loginData.auth.access_token,
                id: this.loginData.user.id
            };

            this.loader.presentLoading().then(() => {
                // fetching groups data
                this.apiService.getGroups(JSON.stringify(body)).subscribe(async (res: any) => {
                if (res.groups && res.groups.length === 0) {
                    this.groupsdata = true;
                } else {
                    this.groupsdata = false;
                    this.data = res.groups;
                    let test;
                    let today;
                    let dd;
                    let mm;

                    const monthNames = ["Jan", "Feb", "Mar", "Apr", "May", "June", "July", "Aug", "Sep", "Oct", "Nov", "Dec"];

                    // formatting date
                    let date = [];
                    for (let x = 0; x < this.data.length; x++) {
                        test = this.data[x].start_date;
                        today = new Date(test);
                        dd = today.getDate();
                        mm = monthNames[today.getMonth()];
                        date.push({dd, mm});
                        this.data[x].start_date = date[x];
                    }
                }
                this.loader.stopLoading();
                this.dataLoaded = true;
            }, (err: any) => {
                let message = err.message;
                this.toast.presentToastWithOptions((this.alertMessage ? this.alertMessage.error_occured_try_again : 'Error Occured, Try Again'), (this.alertMessage ? this.alertMessage.WarningMessage : 'Warning Message'), 'warning');
                this.loader.stopLoading();
                this.dataLoaded = true;
            });
        });
    }

    // navigating to back page
    gotobackPage(){
        if(this.theme.ch_id === '1000'){
            this.menu.toggle();
        } else {
            this.location.back();
        }
    }

    setAnalytics() {
        let dev_id = '';
        let device = 'web';
        // preparing device data
        if(this.platform.is('android') || this.platform.is('iphone')) {
            dev_id = this.device.uuid;
            if (dev_id){
                device =  this.platform.platforms()[0];
            }
        }

        // preparing duration data
        let end_time_analytics = moment();
        var duration:any = moment.duration(end_time_analytics.diff(this.start_time_analytics)).asSeconds();

        // preparing user data
        let userId = this.loginData ? this.loginData.user.id : 'guest';

        // preparing data for analytics
        const body = JSON.stringify({
            church_id: this.theme.ch_id,
            latitude: this.theme.latitude ? this.theme.latitude : "",
            longitude: this.theme.longitude ? this.theme.longitude : "",
            timespent: duration,
            module: this.page_name,
            custom_name: this.custom_name,
            ip_address: "",
            device: device,
            device_id: dev_id,
            client_type: userId,
            event_type: ""
        });
        // calling function from service to update Analytics Data
        this.analytics.setAnalytics(body).subscribe((res: any) => {

        })
    }

    ionViewDidLeave(){
        this.setAnalytics();
    }
    ionViewDidEnter(){
        // getting user data
        this.getLoginData();

        // setting start time for analytics
        this.start_time_analytics = moment();
        this.event_provider.hidefooter(true, 'other');
    }

}
